'use client';

import { useState, useEffect } from 'react';
import {
  Layout,
  Card,
  Button,
  Space,
  Typography,
  Row,
  Col,
  Statistic,
  message,
  Breadcrumb,
  Alert,
  Table,
  Tag,
  Spin
} from 'antd';
import {
  CalendarOutlined,
  PlusOutlined,
  ClockCircleOutlined,
  CheckCircleOutlined,
  CloseCircleOutlined,
  UserOutlined
} from '@ant-design/icons';
import Link from 'next/link';
import { ROUTES } from '@/lib/routes';
import dayjs from 'dayjs';

const { Header, Content } = Layout;
const { Title, Text } = Typography;

interface DashboardData {
  estatisticas: {
    totalFerias: number;
    feriasPendentes: number;
    feriasAprovadas: number;
    feriasReprovadas: number;
  };
  feriasRecentes: Array<{
    id: string;
    colaborador: {
      id: string;
      nome: string;
      cargo: string;
      departamento: string;
    };
    dataInicio: string;
    dataFim: string;
    status: string;
    tipo: string;
    observacoes?: string;
  }>;
}

export default function FeriasPage() {
  const [dashboardData, setDashboardData] = useState<DashboardData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      console.log('Buscando dados do dashboard...');
      const response = await fetch('/api/dashboard');
      console.log('Response status:', response.status);
      
      if (response.ok) {
        const data = await response.json();
        console.log('Dados recebidos:', data);
        setDashboardData(data);
      } else {
        const errorText = await response.text();
        console.error('Erro na resposta:', errorText);
        setError(`Erro ${response.status}: ${errorText}`);
        message.error('Erro ao buscar dados do dashboard');
      }
    } catch (error) {
      console.error('Erro ao buscar dados:', error);
      setError(`Erro de conexão: ${error}`);
      message.error('Erro ao buscar dados');
    } finally {
      setLoading(false);
    }
  };

  const columns = [
    {
      title: 'Colaborador',
      key: 'colaborador',
      render: (record: any) => (
        <div>
          <Text strong>{record.colaborador.nome}</Text>
          <br />
          <Text type="secondary">{record.colaborador.cargo} - {record.colaborador.departamento}</Text>
        </div>
      ),
    },
    {
      title: 'Período',
      key: 'periodo',
      render: (record: any) => (
        <div>
          <Text>{dayjs(record.dataInicio).format('DD/MM/YYYY')} a {dayjs(record.dataFim).format('DD/MM/YYYY')}</Text>
          <br />
          <Text type="secondary">
            {dayjs(record.dataFim).diff(dayjs(record.dataInicio), 'day') + 1} dias
          </Text>
        </div>
      ),
    },
    {
      title: 'Status',
      dataIndex: 'status',
      key: 'status',
      render: (status: string) => {
        const statusConfig = {
          aprovado: { color: 'green', text: 'Aprovado', icon: <CheckCircleOutlined /> },
          pendente: { color: 'orange', text: 'Pendente', icon: <ClockCircleOutlined /> },
          reprovado: { color: 'red', text: 'Reprovado', icon: <CloseCircleOutlined /> }
        };
        
        const config = statusConfig[status as keyof typeof statusConfig] || statusConfig.pendente;
        return <Tag color={config.color} icon={config.icon}>{config.text}</Tag>;
      },
    },
  ];

  const breadcrumbItems = [
    { title: <Link href={ROUTES.DASHBOARD}>Dashboard</Link> },
    { title: 'Férias' }
  ];

  if (loading) {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <div style={{ 
          display: 'flex', 
          justifyContent: 'center', 
          alignItems: 'center', 
          height: '100vh',
          flexDirection: 'column'
        }}>
          <Spin size="large" />
          <Text style={{ marginTop: 16 }}>Carregando dados...</Text>
        </div>
      </Layout>
    );
  }

  if (error) {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <div style={{ 
          display: 'flex', 
          justifyContent: 'center', 
          alignItems: 'center', 
          height: '100vh',
          flexDirection: 'column'
        }}>
          <Text type="danger">Erro: {error}</Text>
          <Button onClick={fetchDashboardData} style={{ marginTop: 16 }}>
            Tentar Novamente
          </Button>
        </div>
      </Layout>
    );
  }

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Header style={{ 
        padding: '0 24px', 
        background: '#fff',
        borderBottom: '1px solid #f0f0f0',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
      }}>
        <Title level={3} style={{ margin: 0, color: '#1890ff' }}>
          Gerenciamento de Férias
        </Title>
        <Space>
          <Link href={ROUTES.FERIAS.SOLICITAR}>
            <Button type="primary" icon={<PlusOutlined />}>
              Nova Solicitação
            </Button>
          </Link>
          <Link href={ROUTES.DASHBOARD}>
            <Button>
              Voltar ao Dashboard
            </Button>
          </Link>
        </Space>
      </Header>
      
      <Content style={{ margin: '24px 16px', padding: 24, background: '#f5f5f5' }}>
        <Breadcrumb items={breadcrumbItems} style={{ marginBottom: 24 }} />
        
        {/* Estatísticas */}
        <Row gutter={[16, 16]} style={{ marginBottom: 24 }}>
          <Col xs={24} sm={12} lg={6}>
            <Card>
              <Statistic
                title="Total de Férias"
                value={dashboardData?.estatisticas.totalFerias || 0}
                prefix={<CalendarOutlined />}
                valueStyle={{ color: '#1890ff' }}
              />
            </Card>
          </Col>
          <Col xs={24} sm={12} lg={6}>
            <Card>
              <Statistic
                title="Férias Pendentes"
                value={dashboardData?.estatisticas.feriasPendentes || 0}
                prefix={<ClockCircleOutlined />}
                valueStyle={{ color: '#faad14' }}
              />
            </Card>
          </Col>
          <Col xs={24} sm={12} lg={6}>
            <Card>
              <Statistic
                title="Férias Aprovadas"
                value={dashboardData?.estatisticas.feriasAprovadas || 0}
                prefix={<CheckCircleOutlined />}
                valueStyle={{ color: '#52c41a' }}
              />
            </Card>
          </Col>
          <Col xs={24} sm={12} lg={6}>
            <Card>
              <Statistic
                title="Férias Reprovadas"
                value={dashboardData?.estatisticas.feriasReprovadas || 0}
                prefix={<CloseCircleOutlined />}
                valueStyle={{ color: '#ff4d4f' }}
              />
            </Card>
          </Col>
        </Row>

        {/* Ações Rápidas */}
        <Row gutter={[16, 16]} style={{ marginBottom: 24 }}>
          <Col xs={24} lg={12}>
            <Card title="Ações Rápidas">
              <Space direction="vertical" style={{ width: '100%' }}>
                <Link href={ROUTES.FERIAS.SOLICITAR}>
                  <Button type="primary" icon={<PlusOutlined />} block>
                    Solicitar Férias
                  </Button>
                </Link>
                <Link href={ROUTES.FERIAS.LISTAR}>
                  <Button icon={<CalendarOutlined />} block>
                    Ver Todas as Solicitações
                  </Button>
                </Link>
                <Link href={ROUTES.COLABORADORES.LISTAR}>
                  <Button icon={<UserOutlined />} block>
                    Gerenciar Colaboradores
                  </Button>
                </Link>
              </Space>
            </Card>
          </Col>
          <Col xs={24} lg={12}>
            <Card title="Solicitações Recentes">
              <Table
                columns={columns}
                dataSource={dashboardData?.feriasRecentes || []}
                rowKey="id"
                pagination={false}
                size="small"
                scroll={{ y: 200 }}
              />
            </Card>
          </Col>
        </Row>
      </Content>
    </Layout>
  );
}
